const About = () => {

function getAge() {
    let date = new Date();
    return date.getFullYear() - 2002;
}

    return (
        <div className="about">
            <h2>O mnie</h2>
            <p>Witaj! Jestem Daniel i pochodzę z Wrocławia. Mam ukończone {getAge()} lat. Obecnie jestem w klasie maturalnej w Technikum Akademickim przy Międzynarodowej Wyższej Szkole Logistyki i Transportu we Wrocławiu i zamierzam w niedalekiej przyszłości dostać się na Politechnikę Wrocławską. Oprócz programowania interesuję się komunikacją miejską oraz transportem ciężarowym. Właśnie dlatego jestem w zarządzie wirtualnej firmy Coffee-Spedition, która opiera się na grze Euro Truck Simulator 2. To właśnie dzięki tej firmie mam inspiracje do tworzenia aplikacji mobilnej oraz strony internetowej które to od 2 lat po dziś dzień są udoskonalane.</p>
        </div>
    )
}

export default About
